@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400&family=Libre+Franklin:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Work+Sans:wght@300&display=swap);
/** FONTS **/

html{
  scroll-behavior: smooth;;
}

body {
  margin: 0;
  width: 100%;
  height: 100% !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  height: 100% !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/** SPACING **/ 
p {
  line-height: 70px;   /* within paragraph */
  margin-bottom: 10px; /* between paragraphs */
}

/** NAVBAR **/ 
.center-navbar {
  width:30%;
  margin-left: 35%;
}


.basic-navbar-nav {
  width: 100%;
  text-align: center;
}

/** HOME PAGE **/ 
.homePage {
  height: 100%;
}

.jumbotronImg {
  background-image: url(/static/media/angela.ed56a431.jpg); 
  max-width: 100%;
  height: 800px; 
  background-position: left;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
}

.homeText {
  color: #302425;
  font-family: 'Poppins', sans-serif;
  font-size: 80px;
  font-weight: bold;
  text-shadow: 0px 0px 25px #ffffff;

  margin-left: auto; 
  margin-right: 0; 
  text-align: center;
  height: 100%;
  padding-top: 15%;
  padding-right: 10%;
  padding-left: 10%;
  background-color: rgba(255, 255, 255, 0.3);
}

#secondText {
  font-size: 55px;
  font-weight: lighter !important;
  font-weight: normal;
  text-shadow: 0px 0px 25px #ffffff;
}

.downloadCvButton {
  color: rgba(48, 36, 37, 0.7) !important;
  font-weight: lighter !important;
  font-weight: bold;
  font-family: 'Work Sans', sans-serif !important;


  font-size: 25px !important;
  background-color: rgba(255, 255, 255, 0.25) !important; /* Green */
  border: 3px solid #302425  !important;
  border-color:rgba(48, 36, 37, 0.7) !important;
}


/** ABOUT **/ 
.aboutBody {
  height: 900px;

}

/** EXPERIENCE **/

.experienceBody {
  height: 900px;

}

/** CONTACT **/ 

.contactBody {
  height: 900px;
}
.App {
  
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
